@use "global" as *;

.p-works {
  background: linear-gradient(90deg,#fff 0%,#fff 30.7%,rgba(#505C4B80, .5) 30.7%,rgba(#505C4B80, .5) 100%);
  @include mq(md) {
    background: linear-gradient(90deg,#fff 0%,#fff 50%,rgba(#505C4B80, .5) 50%,rgba(#505C4B80, .5) 100%);
  }
}

.p-works__inner {
  padding-top: rem(16);
  @include mq(md) {
    padding-top: rem(152);
  }
}

.p-works__title {
  margin-left: auto;
}

.p-works__text {
  margin-top: rem(8);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-works__swiper {
  margin-left: rem(27);
  margin-top: rem(18);
  @include mq(md) {
    margin-top: rem(77);
    margin-left: 0;
  }
}

.p-works__slide {
  margin-top: 0;
  max-width: rem(320);
}

.p-works__slide + .p-works__slide {
  margin-top: 0;
}

.p-works__button {
  margin-top: rem(60);
  text-align: center;
  @include mq(md) {
    margin-top: rem(85);
  }
}